body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  outline: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div {
  color: black;
}

h2 {
  font-size: 20px;
  width: 450px;
  color: black;
  text-align: center;
}

.App {
  background-color: #ffffff; /*  #282c34 */
  min-height: 100vh;
  height: 100%;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.OptionText {
  font-family: OpenSans-Semibold;
  font-size: 15px;
  color: #496472;
  letter-spacing: 0;
  /* text-align: center; */
}

.Rectangle {
  background: #fafafa;
  border: 2px solid #d9e6eb;
  border-radius: 23px;
}

.Rectangle:hover {
  box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
}

.ReactangleBig {
  width: 570px;
  height: 164px;
}

.ReactangleSmall {
  width: 190px;
  height: 55px;
}

.RectangleSelect {
  border: 4px solid #0d7bab;
}

.RectangleCorrect {
  border: 4px solid #33c933;
}

.RectangleWrong {
  border: 4px solid #fb4343;
}

.Text {
  display: flex;
  justify-content: center;
  align-items: center;
}

.QuestionsContainer {
  display: flex;
  flex-wrap: wrap;
  width: 450px;
  height: 200px;
  align-content: space-around;
  justify-content: space-aroun;
}

.circle {
  width: 10px;
  height: 10px;
  -webkit-border-radius: 25px;
  -moz-border-radius: 25px;
  border-radius: 25px;
  background: #c9cfd3;
}

.circleCurrent {
  background: #0d7bab;
}

.okButton {
  background: #f0f0f0;
  border-radius: 100px;
}

.okText {
  color: #ffffff;
}

.okSelected {
  background: #ff8f48;
  border-radius: 100px;
}

.okSelected:hover {
  background: #f68034;
  border-radius: 100px;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.creditIcon {
  width: 35px;
  height: 35px;
}
